html,body{
    margin: 0;
    font-family: "PingFang SC","Microsoft Yahei",sans-serif;
  }

  .container{
    width: 100vw;
    height: 100vh;
    display: table;
    align-items: center;
    justify-content: center;
    background: url("../img/background.jpg") fixed no-repeat;
    background-size: 100% 100%;
  }

  .logo img{
    display: block;
    margin: 88px auto 0;
    width: 200px;
    height: 100px;
}

  .login-form{
    width: 240px;
    height: 320px;
    margin: 20px auto;
    display: flex;
    flex-direction: column;
    padding: 40px;
    text-align: center;
    position:relative;
    z-index: 100;
    background: inherit;
    border-radius: 18px;
    overflow: hidden;
  }

  .login-form::before{
    content: "";
    width: calc(100% + 20px);
    height: calc(100% + 20px);
    position: absolute;
    top: -10px;
    left: -10px;
    overflow: hidden;
    background: inherit;
    box-shadow: inset 0 0 0 200px rgba(255, 255, 255, 0.25);
    filter: blur(5px);
    z-index: -1;
  }

  .login-form h2{
    font-size: 18px;
    font-weight: 400;
    color: #3d5245;
  }

  .login-form input,
  .login-form button{
    margin: 6px 0;
    height: 36px;
    border: none;
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: 4px;
    padding: 0 14px;
    color: #3d5245;
  }
  .login-form input::placeholder{
    color: #3d5245;
  }
  .login-form button:focus,
  .login-form input:focus{
    outline: 0;
  }

  .login-form button{
    margin: 8px;
    background-color: rgba(209, 179, 9, 0.4);
    color: white;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    transition: 0.4s;
  }

  .login-form button:hover{
    background-color: rgba(209, 179, 9 , 0.7);
  }

  .login-form button::before,
  .login-form button::after{
    content: "";
    display: block;
    width: 80px;
    height: 100%;
    background: rgba(179, 255, 210, 0.5);
    opacity: 0.5;
    position: absolute;
    top: 0;
    left: 0;
    transform: skewX(-15deg);
    filter: blur(30px);
    overflow: hidden;
    transform: translateX(-100px);
  }

  .login-form button:hover::after{
    width: 40px;
    background: rgba(179, 255, 210, 0.3);
    left: 60px;
    opacity: 0;
    filter: blur(5px);
  }
  .login-form button:hover::before{
  transition: 1s;
  transform: translateX(320px);
  opacity: 0.7s;
  }
  .login-form button:hover::after{
    transition: 1s;
    transform: translateX(320px);
    opacity: 1;
  }

  .radio-item .radio{
          display: inline-block;
          width: 12px;
          height: 12px;
          border:1px solid rgb(0, 0, 0);
          border-radius: 50%;
  }

  .item{
    margin: 6px 0;
    height: 25px;
    border: none;
    font-size: 14px;
  }

  .radio-item input:checked+.radio{
      border-color:#008c8c ;
  }

  .radio-item input:checked+.radio::after{
      content: "";
      display: block;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background: #008c8c;
      margin-top:3px ;
      margin-left: 3px;
  }

  .radio-item input:checked~span{
      color: #008c8c;
  }

  .radio-item input[type="radio"]{
      display: none;
  }
